import uniNavBar from "../../components/uni-nav-bar/uni-nav-bar.vue";
import LoadImages from '../../components/upload-images.vue';
import uniPopup from "../../components/uni-popup/uni-popup.vue";
let itemList = ["所有人可见","仅自己可见"]
export default {
	components:{
		uniNavBar,
		LoadImages,
		uniPopup
	},
	data() {
		return {
			isget:false,
			lookMessage:itemList[0],
			text:"",
			imageList:[],
				showPopup: true,
				content:""
		}
	},
	// 监听返回按钮
	onBackPress(){
		if(!this.text && this.imageList.length < 1) {return false}
		if(!this.isget){
			this.baocun()
			return true;
		}
	},
	methods: {
		//保存为草稿
		baocun(){
			uni.showModal({
				content:"是否要保存为草稿?",
				cancelText:"不保存",
				confirmText:"保存",
				success: (res) => {
					if(res.confirm){
						console.log("保存")
					}else{
						console.log("不保存")
					}
					this.isget= true
					uni.navigateBack({
						delta:1
					})
				}
			})
		},
		// 返回
		back(){
			// console.log(this.showPopup)
			uni.navigateBack({
				delta:1
			})
		},
		// 发布
		submit(){
			
		},
		// 隐私
		changelook(){
			uni.showActionSheet({
				itemList:itemList,
				success:(e)=>{
					this.lookMessage = itemList[e.tapIndex]
					
				},
				fail:()=>{
					
				}
			})
		},
		upload(imageList){
			this.imageList = imageList
		},
		changeTogel(e){
			this.showPopup = e.show;
		},
		hidePopup(){
			this.showPopup = false;
		}
	}
}
